declare global {
  interface Window {
    dataLayer: any[]
    Shopify: any
    clarity: any
    hj: any
    tippy: any
    [key: string]: any
    Flickity: any
  }
}

type eventType =
  | 'Line'
  | 'Link'
  | 'Backdrop'
  | 'Accordion'
  | 'Sticky button'
  | 'Button'
  | 'Radio'
  | 'Section'
  | 'Click'
  | 'Visibility'
  | 'submit'
  | 'Input'
  | 'Change'
  | 'Pagination'
  | 'error'
  | 'success'
  | 'other'
  | 'Arrows'
  | 'Swipe slider'
  | 'Experiment activated'
  | 'Dropdown'
  | 'View'

export const pushData = (name: string, desc: string, type: eventType, loc: string = '') => {
  window.dataLayer = window.dataLayer || []
  window.dataLayer.push({
    event: 'event-to-ga4',
    event_name: name,
    event_desc: desc,
    event_type: type,
    event_loc: loc
  })
  console.dir(`Event: ${name} | ${desc} | ${type} | ${loc}`)
}

interface StartLog {
  name: string
  dev: string
}

export const startLog = ({ name, dev }: StartLog) => {
  console.log(
    `%c EXP: ${name} (DEV: ${dev})`,
    `background: #3498eb; color: #fccf3a; font-size: 20px; font-weight: bold;`
  )
}

export const $$el = selector => document.querySelectorAll(selector)
export const $el = selector => document.querySelector(selector)

// load list of scripts
export const loadScriptsOrStyles = async (urls: string[]) => {
  const loadScriptOrStyle = (url: string) => {
    return new Promise((resolve, reject) => {
      // check script by document.scripts
      const type = url.split('.').pop()
      if (type === 'js') {
        const loadedScripts = Array.from(document.scripts).map(script => script.src.toLowerCase())
        if (loadedScripts.includes(url.toLowerCase())) {
          console.log(`Script ${url} allready downloaded!`)
          return resolve('')
        }
        const script = document.createElement('script')
        script.src = url
        script.onload = resolve
        script.onerror = reject
        document.head.appendChild(script)
      } else if (type === 'css') {
        const loadedStyles = Array.from(document.styleSheets).map(style => style.href?.toLowerCase())
        if (loadedStyles.includes(url.toLowerCase())) {
          console.log(`Style ${url} allready downloaded!`)
          return resolve('')
        }
        const style = document.createElement('link')
        style.rel = 'stylesheet'
        style.href = url
        style.onload = resolve
        style.onerror = reject
        document.head.appendChild(style)
      }
    })
  }

  for (const url of urls) {
    await loadScriptOrStyle(url)
    console.log(`Loaded librari ${url}`)
  }
  console.log('All libraries loaded!')
}

// clarity
export const clarityInterval = (name: string, variant = 'variant_1') => {
  let int = setInterval(function () {
    if (typeof window.clarity == 'function') {
      clearInterval(int)
      window.clarity('set', name, variant)
      console.log('set', name, variant)
    }
  }, 1000)
}

export const hjInterval = name => {
  let int = setInterval(function () {
    if (typeof window.hj == 'function') {
      clearInterval(int)
      window.hj('event', name)
    }
  }, 1000)
}

// visibility element
export const visibilityOfTime = (
  selector: string | HTMLElement | Element,
  eventName: string,
  visiblePlace: string,
  description?: string,
  visibilityTxt = 'Visibility',
  time = 600,
  threshold = 0.5
) => {
  let observer: IntersectionObserver
  let timer: NodeJS.Timeout

  observer = new IntersectionObserver(
    function (entries) {
      if (entries[0].isIntersecting === true) {
        timer = setTimeout(() => {
          pushData(
            eventName,
            (entries[0].target as HTMLElement).dataset.visible || description || '',
            visibilityTxt as eventType,
            visiblePlace
          )
          observer.disconnect()
        }, time)
      } else {
        // console.log('Element is not fully visible')
        clearTimeout(timer) // Clear the timer if the element is not visible
      }
    },
    { threshold: [threshold] }
  )

  if (typeof selector === 'string') {
    const element = document.querySelector(selector)

    if (element) {
      observer.observe(element)
    }
  } else {
    observer.observe(selector)
  }
}

// waitForElement
export function waitForElement(selector: string) {
  return new Promise(resolve => {
    if (document.querySelector(selector)) {
      return resolve(document.querySelector(selector))
    }

    const observer = new MutationObserver(() => {
      if (document.querySelector(selector)) {
        resolve(document.querySelector(selector))
        observer.disconnect()
      }
    })

    observer.observe(document.documentElement, {
      childList: true,
      subtree: true,
      characterData: true
    })
  })
}

export const checkScrollSpeed = (function (settings) {
  settings = settings === undefined ? {} : (settings as any)

  let lastPos: number | null
  let newPos: number | null
  let timer: string | number | NodeJS.Timeout | undefined
  let delta: number
  let delay = (settings as any)?.delay || 50

  function clear() {
    lastPos = null
    delta = 0
  }

  clear()

  return function () {
    newPos = window.scrollY
    if (lastPos != null) {
      delta = newPos - lastPos
    }
    lastPos = newPos
    clearTimeout(timer)
    timer = setTimeout(clear, delay)
    return delta
  }
})()
function delay(x: number) {
  return new Promise(resolve => setTimeout(resolve, x))
}
export const checkScrollPosition = async (headerOff: number, elPosition: HTMLElement) => {
  const headerOffset = headerOff
  const elementPosition = elPosition?.getBoundingClientRect().top
  const offsetPosition = elementPosition + window.pageYOffset - headerOffset

  window.scrollTo({
    top: offsetPosition,
    behavior: 'smooth'
  })

  await delay(800)
  return true
}

export const getCookies = (name: string) => {
  const value = `; ${document.cookie}`
  if (value.includes(`; ${name}=`)) {
    const parts = value.split(`; ${name}=`)
    return parts.pop()?.split(';').shift()
  } else {
    console.log('No cookie')
    return null
  }
}

export const scrollToElement = (selector: string, offset: number = 100) => {
  const element = document.querySelector(selector)
  if (!element) return
  const elementPosition = element.getBoundingClientRect().top
  const offsetPosition = elementPosition - offset
  window.scrollBy({
    top: offsetPosition,
    behavior: 'smooth'
  })
}

export const scrollToHtmlElement = (element: HTMLElement, offset: number = 100) => {
  const elementPosition = element.getBoundingClientRect().top + window.scrollY
  const offsetPosition = elementPosition - offset
  window.scrollTo({
    top: offsetPosition,
    behavior: 'smooth'
  })
}
